import React, { Component } from 'react';
import './openredbag.css';
import '../../style.css';
import {openRedBagBg,canleIon,superRedbacket2x} from '../../public';
import GetBagList from './getbaglist';
class OpenRedBag extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	sysNum:"0sys",
    	data:this.props.data,
    	rePaperList:[]
    }
    this.rePaperList=[]
    this.sysNum="0SYS"
  }
	componentWillMount(){
		let _this=this;
//		let lablePropsArr=_this.props.data.getRedPaperInfoArr;
		let lablePropsArr=_this.state.data;
		alert(JSON.stringify(this.props.data));
		
		console.log('++++++++++``componentWillMount````+++++++',_this.props.data[0]);
		let lableArr=[];
		for(let i=0;i<lablePropsArr.length;i++){
			let obj=lablePropsArr[i];
			//obj.re_player=='gre53palyer3'?_this.setState({sysNum:obj.re_amount}):'';
			let user_name=localStorage.getItem('account_name');
			if(user_name==obj.re_player){
				_this.setState({sysNum:obj.re_amount})
			}
			lableArr=lableArr.concat([<GetBagList key={i} keys={i} data={obj}></GetBagList>])
		}
		_this.setState({rePaperList:lableArr})
	}
	componentWillReceiveProps(){
		console.log('-=componentWillReceiveProps-=');
//		let _this=this;
//
//		let lablePropsArr=_this.props.data;
//
//		let lableArr=[];
//		for(let i=0;i<lablePropsArr.length;i++){
//			let obj=lablePropsArr[i];
//			
//			lableArr=lableArr.concat([<GetBagList key={i} keys={i} data={obj}></GetBagList>])
//		}
//		_this.rePaperList=lableArr
	}
	cancel(){
		this.props.callBack("cancelRedBag");
	}
	render(){
		let lang=localStorage.getItem("lang");
		lang=lang?lang:"CN";

		
		return(
			<div className="open-wrap">
				<div className="open-box">
					<div className="img-box">
						<img src={openRedBagBg}/>
					</div>
					<div className="open-intro">
						<div className="cancel-box">
							<span onClick={this.cancel.bind(this)} className="glyphicon glyphicon-remove"></span>

						</div>
						<img className="bag-icon" src={superRedbacket2x}/>
						<p className="p1">{lang=="CN"?"超级红包":"Super Hongbao"}</p>
						<p className="all-eos">{lang=="CN"?"你共获得":"You grabbed"}{this.state.sysNum}</p>
					</div>
					{this.state.rePaperList}

				</div>
			</div>
		)
	}
}

export default OpenRedBag;