import React, { Component } from 'react';
import {shbIcon,eosIcon,boxBg,superRedbacket2x,redBagIcon} from '../../public'

class RedBag extends Component{
	constructor(props) {
    super(props);
    this.state = {
 
    };
    
 }
componentWillMount(){
//  this.props.callBack('9----09090****90888888**');
 }
	render(){
		let lang=localStorage.getItem("lang");
		lang=lang?lang:"CN";
		return (
			<div className="red-bag-wrap flex">
				<div className="left img-box">
					<img src={superRedbacket2x}/>
				</div>
				<div className='right img-box' onClick={this.props.callBack.bind(this)}>
					<img src={boxBg}/>
					<div className='r-top'>
						<img src={redBagIcon}/>
						<span>{lang=="CN"?"红包已被领取"+this.props.num+"个":this.props.num+" Hongbao Grabbed"}</span>
					</div>
					<div className="r-bottom">eos红包</div>
					
				</div>
			
			</div>
			
		)
	}
}
export default RedBag;