import React, { Component } from 'react';
import {loadingIcon} from '../../public';
import './loading.css';
class Loading extends Component{
	constructor(props) {
    super(props);
    
 
    
  }
	cancel(e){
		this.props.callBack('cancelInvite');
	}
	componentWillMount(){
		let _this=this;
		var num=0;
		
	}
	render(){
		
		return (
			<div className="loading-wrap">
				<div className="img-box">
					<img src={loadingIcon}/>


				</div>
			</div>
		)
	}
}
export default Loading;