import React, { Component } from 'react';

class GetBagList extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	
    };
    
  }
	render(){
		console.log(this.props.data,'打开红包包后数据.....');
		let lang=localStorage.getItem("lang");
		lang=lang?lang:"CN";
		let cn=this.props.data.re_player+"获得了"+this.props.data.re_amount+"和"+Math.floor(this.props.data.re_pos/3)+"个sup";
		let en=this.props.data.re_player+"grabbed"+this.props.data.re_amount+" and"+Math.floor(this.props.data.re_pos/3)+"sup";
		return(
			<div className="get-bag-list-wrap">
				<div className="list flex-around">
					<span>{this.props.keys+1}</span>
					<span>{lang=="CN"?cn:en}</span>
					
				</div>
			</div>
		)
	}
}

export default GetBagList;