import React, { Component } from 'react';
import './openredbag.css';
import '../../style.css';
import {boaOpen,boaUserPic,boaBg} from '../../public';
class OpenBoaBag extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	openClass:'bag-icon boa-icon boa-pen-icon'
    };
    
  }
	cancelBag(){
		this.props.callBack('cancelBoaBag');
	}
	openBag(){
		let classN='bag-icon boa-icon boa-pen-icon rotateYXs';//添加旋转
		this.setState({openClass:classN});
		this.props.callBack('openRedBag');
	}
	render(){
		let lang=localStorage.getItem("lang");
		lang=lang?lang:"CN";
		return(
			<div className="open-wrap">
				<div className="open-box">
					<div className="img-box">
						<img src={boaBg}/>
					</div>
					<div className="open-intro">
						<div className="cancel-box">
							<span onClick={this.cancelBag.bind(this)} className="boa-cancel glyphicon glyphicon-remove"></span>

						</div>
						<img className="bag-icon boa-icon" src={boaUserPic}/>
						<p className="boa-p1 p1">{lang=="CN"?"超级红包":"Super Hongbao"}</p>
						<p className="all-eos">{lang=="CN"?"随机获取幸运大奖":"Random access to the Lucky Awards"}</p>
						<img onClick={this.openBag.bind(this)} className={this.state.openClass} src={boaOpen}/>
					</div>
				</div>
			</div>
		)
	}
}

export default OpenBoaBag;