import React, { Component } from 'react';
import {shbIcon,eosIcon,bg2x,boaIcon} from '../../public'
import './roomMess.css';
import Saldo from './saldo';
import RedBag from './redbag';
import RedBagEnd from './redbagend';

class RoomMess extends Component{
	constructor(props) {
    super(props);
    this.state = {
 		boaNum:this.props.boaNum,
 		elemUI:[],
 		reID:-1,//红包id
 		reState:-1
    };
    this.updateUI=this.updateUI.bind(this);
 }
componentWillMount(){
//  this.props.callBack('9----09090****90888888**');
 }
openBag(end){
	if(end>0){
		alert('红包已被领完了！');
		return '';
	}
	this.props.callBack('openBag');
}
openBoaBag(){
	this.props.callBack('openBoaBag');

}
openRule(){
	this.props.callBack('openAlerts');
}
updateUI(lang){

	let _this=this;
	let endArr=[];//红包抢完UI
	let reNum=_this.props.data.getRedPaperInfoArr.length;//红包领取个数

	if(_this.props.state.redPaperState<=0&&!_this.props.data.isEnd){//红包失效

		return false;
	}else if(_this.props.state.redPaperState<=0&&_this.props.data.isEnd){
		endArr=[<RedBagEnd></RedBagEnd>];
//		reNum=3;
	}
	
	let isCreate=(_this.props.state.redPaperId!=_this.state.reId)&&(_this.state.reId!=-1);
	_this.setState({
		reId:_this.props.state.redPaperId
	});

	let arr=_this.state.elemUI;
	if(!isCreate){//更新 ，不用末尾追加
		arr.pop();
	}
	let lableArr=[];
	let lablePropsArr=_this.props.data.getRedPaperInfoArr;
	
	for(let i=0;i<lablePropsArr.length;i++){
		let obj=lablePropsArr[i];
		let cn=obj.re_player+"获得了"+obj.re_amount+"和"+Math.floor(obj.re_pos/3)+"个sup";
		let en=obj.re_player+"grabbed"+obj.re_amount+" and"+Math.floor(obj.re_pos/3)+"sup";
		lableArr=lableArr.concat([<span className="label label-default">{lang=="CN"?cn:en}</span>])
	}
	
	let arr1=[<div>
			<span className="time"></span>
				<RedBag num={reNum} callBack={_this.openBag.bind(this,endArr.length)}></RedBag>
				{lableArr}
				{endArr}
		</div>]
	arr=arr.concat(arr1);
	_this.setState({
		
		reState:_this.props.state.redPaperState,
		elemUI:arr
	})

}
componentWillReceiveProps(){
	let lang=localStorage.getItem("lang");
		lang=lang?lang:"CN";
	let iconSrc=this.props.boaNum>0?boaIcon:boaIcon;
	this.updateUI(lang);
	
}
	render(){
		
		let lang=localStorage.getItem("lang");
		lang=lang?lang:"CN";
		let boaNum=this.props.boaNum;
		boaNum=parseFloat(boaNum);
		boaNum=Math.floor(boaNum/3);
		let iconSrc=boaNum>0?boaIcon:boaIcon;


//		this.updateUI(lang);
		return (
			<div className="room-wrap-box">
				<Saldo></Saldo>
				
				{this.state.elemUI}
				<div className="boa-red-bag">
					<div className="img-box" onClick={this.openBoaBag.bind(this)}>
						<img src={boaIcon}/>
						<span className="boa-title">{lang=="CN"?"幸运红包":"Lucky"}</span>
						<span>{lang=="CN"?"剩余"+boaNum+"次":boaNum+" remaining"}</span>
					</div>
					<span className="boa-rule" onClick={this.openRule.bind(this)}>{lang=="CN"?"幸运红包规则":"Lucky Hongbao Rule"}</span>
				</div>
			</div>
			
		)
	}
}
export default RoomMess;