import React, { Component } from 'react';
import '../../style.css';
import './alerts.css';
class Alerts extends Component{
	constructor(props) {
    super(props);
    this.state = {

    };
    
  }

	render(){
		let lang=localStorage.getItem("lang");//语种
  		lang=lang?lang:"CN";
		return (
			<div className="alert-wrap">
				<div className="alert-box">
					<div className="alert-cancel">
					<span onClick={this.props.callBack.bind(this)} className="glyphicon glyphicon-remove"></span>

					</div>
					<div className="alert-title">{lang=="CN"?"幸运池规则":"Lucky pool rule"}</div>
					<div className="alert-content">
						{lang=="CN"?"用户每参与3局抢红包游戏,将会获得一次打开幸运红包机会.每个红包含有(0.05-0.08)*房间等级的eos	":"Users will get a chance to open the lucky red packet for every 3 games. Each red packet contains (0.05-0.08) * room grade eos"}
					</div>
				</div>
			</div>
		)
	}
}
export default Alerts;