import React, { Component } from 'react';
import './openredbag.css';
import '../../style.css';
import {openIcon,superRedbacket2x,openBg} from '../../public';
class OpenBag extends Component{
	constructor(props) {
    super(props);
    this.state = {
    	openClass:'bag-icon boa-icon boa-pen-icon'
    };
    
  }
	cancelBag(){
		this.props.callBack('cancelOpenBag');
	}
	openBag(){
		let classN="bag-icon boa-icon boa-pen-icon rotateYXs" ;//添加旋转
		this.setState({openClass:classN});
		this.props.callBack('openRedBag');
	}
	render(){
		let lang=localStorage.getItem("lang");
		lang=lang?lang:"CN";
		return(
			<div className="open-wrap open-bag">
				<div className="open-box">
					<div className="img-box">
						<img src={openBg}/>
					</div>
					<div className="open-intro">
						<div className="cancel-box">
							<span onClick={this.cancelBag.bind(this)} className="boa-cancel glyphicon glyphicon-remove"></span>

						</div>
						<img className="bag-icon boa-icon open-icon" src={superRedbacket2x}/>
						<p className="boa-p1 p1">{lang=="CN"?"超级红包":"Super Hongbao"}</p>
						<p className="all-eos">EOS {lang=="CN"?"红包":"Hongbao"}</p>
						<img onClick={this.openBag.bind(this)} className={this.state.openClass} src={openIcon}/>
					</div>
				</div>
			</div>
		)
	}
}

export default OpenBag;