import React, { Component } from 'react';
import '../style.css';
import './room.css';
import Nav from '../comment/nav/nav';
import RoomMess from '../comment/roomMess/roomMess';
import OpenRedBag from '../comment/openredbag/openredbag';
import OpenBoaBag from '../comment/openredbag/openboabag';
import Alerts from '../comment/alerts/alerts';
import OpenBag from '../comment/openredbag/openbag';
import Loading from '../comment/loading/loading';
import RedBag from '../comment/roomMess/redbag';
import RedBagEnd from '../comment/roomMess/redbagend';
import EOS from 'eosjs';

const TABLE_RED_PAPER="redenvelope";//红包表
const TABLE_RED_STATE="gameruntime";//红包状态表
const TABLE_GET_RED_PAPER="grabre";//抢红包表
const TABLE_GAME_CONFIG="gameconf";//游戏配置表
const TABLE_PLAY_BALANCE="playbalance";//游戏玩家表
let contract_name="gre12345game";
let account_name="gre53palyer3";
let account_name2="gre53palyer2";
let account_name3="gre53palyer3";

const CONFIG={
	httpEndpoint:'http://www.super.lighting:8888',
	code_as_wasm: true,
	chain_id:'cf057bbfb72640471fd910bcb67639c22df9f92470936cddc1ade0e2f2e7dc4f',
	
//	keyProvider:["PW5KAHS48gxwJeGjd2xh8RMqDpbPSH2SeLuYBNBdpoM2HyL6pt6W2"],
	keyProvider:['5KQwrPbwdL6PhXujxW37FSSQZ1JiwsST4cqQzDeyXtP79zkvFD3','5Jmsawgsp1tQ3GD6JyGCwy1dcvqKZgX6ugMVMdjirx85iv5VyPR']
}
class App extends Component {
	constructor(props) {
    super(props);
    this.state = {
    	openRedBag:[],
    	openBoaBag:[],
    	openAlerts:[],
    	openBag:[],
    	openRedBagNum:0,
    	boaRedBagNum:0,
    	addLoading:[],
    	getRedPaperInfoArr:[]//领取红包详情
    	,elemUI:[]
		,isEnd:false//红包是否被领完
		,giveRedPaperBtn:[]
		,sysArr:[]//用户抢得了多少sys
		,re_pos:0
		,stopCheckData:0
    };
    this.gameState={
    	redPaperState:0,
    	redPaperId:-1,
    	redSalt:-1,
    	sysObj:{},
    	account_balance:0
    }
    this.checkGameRunTimeTable=this.checkGameRunTimeTable.bind(this);
    this.checkRedenvelope=this.checkRedenvelope.bind(this);
    this.loadRedPaper=this.loadRedPaper.bind(this);
    this.loadGetRedPaperInfo=this.loadGetRedPaperInfo.bind(this);
    this.initPage=this.initPage.bind(this);
    this.getRedPaper=this.getRedPaper.bind(this);
    this.giveRedPaper=this.giveRedPaper.bind(this);
    this.checkAccountBalance=this.checkAccountBalance.bind(this);
  }
	initPage(){
		
		let _this=this;
		_this.redBagClick('openLoading');
		_this.checkGameRunTimeTable(0);//第一次进入页面是否有可抢红包
		_this.checkRedenvelope(0);//最后一个红包ID
		_this.loadRedPaper(0);//加载红包领取情况
		_this.checkAccountBalance(0);//获取帐余额
		
		var count=0;
		var stopUI=setInterval(()=>{
			count++;
			console.log('loading  UI ...',count);
			if(_this.gameState.redPaperState==1){
				clearInterval(stopUI);
//				_this.createUI();
				_this.setState({giveRedPaperBtn:[]})
				_this.redBagClick('cancelLoading');
			}else if(count>10){
				clearInterval(stopUI);
				_this.setState({giveRedPaperBtn:[<div className="re-paper-btn" onClick={_this.giveRedPaper.bind(_this)}>发红包</div>]})
				_this.redBagClick('cancelLoading');
			}
		},1500);
		
	}
	checkGameRunTimeTable(){
		let eos=EOS(CONFIG);
		let _this=this;
		eos.contract(contract_name).then(contract=>{
			eos.getTableRows({"scope":contract_name,"code":contract_name,"table":TABLE_RED_STATE,"json":true}).then(res=>{

				_this.gameState.redPaperState=res.rows[0].grt_game_state;
				_this.gameState.redSalt=res.rows[0].grt_re_salt;

			})
		})
	}
	checkRedenvelope(row){//获取红包ID;
		let eos=EOS(CONFIG);
		let _this=this;
		eos.contract(contract_name).then(contract=>{
			eos.getTableRows({"scope":contract_name,"code":contract_name,"lower_bound":row*10+'',"table":TABLE_RED_PAPER,"json":true}).then(res=>{
				if(res.more){
					_this.checkRedenvelope(row+1);
					return false;
				}
				var obj=res.rows.pop();
				_this.gameState.redPaperId=obj.re_id;
				console.log('红包idsssssssss',obj.re_id);
		
			})
		})
	}
	checkAccountBalance(row){
		let eos=EOS(CONFIG);
		let _this=this;
		let account_name=localStorage.getItem("account_name");
		eos.contract(contract_name).then(contract=>{
			eos.getTableRows({"scope":contract_name,"code":contract_name,"lower_bound":row*10+'',"table":TABLE_PLAY_BALANCE,"json":true}).then(res=>{
				let data=res.rows;
				let isFind=true;//是否找到账余额
				for(let i=0;i<data.length;i++){
					if(account_name==data[i].player){
						_this.gameState.account_balance=data[i].balance;
						isFind=false;
						break;
					}
				}
				if(res.more&&!isFind){//翻页 继续找
					_this.checkAccountBalance(row+1);
				}
				
			})
		})
	}
	loadRedPaper(){
		/**加载红包，也可没有红包*/
		var _this=this;
		var stop,num=0;
		stop=setInterval(()=>{
			num++;
		
			if(_this.gameState.redPaperState==1){//有红包
				clearInterval(stop);
				//加载红包的领取情况
				console.log('有待抢红包....')
				
				_this.loadGetRedPaperInfo(0);
			}else if(num>=10){//没有红包
				clearInterval(stop);
			}
		},500);
		
	}
	loadGetRedPaperInfo(row){//加载红包领取情况

		let eos=EOS(CONFIG);
		let _this=this;
		let re_pos;
		eos.contract(contract_name).then(contract=>{
			
			eos.getTableRows({"scope":contract_name,"code":contract_name,"lower_bound":row*10+'',"table":TABLE_GET_RED_PAPER,"json":true}).then(res=>{
				if(res.more){
					let data=res.rows;
//					let arr=_this.state.getRedPaperInfoArr;
					let arr=[];
					for(var i=0;i<data.length;i++){
						let obj=data[i];
						if(obj.re_id==_this.gameState.redPaperId){
							arr.push(obj);
							if(account_name==obj.re_player){
								_this.setState({sysArr:[obj]})
							}
							//account_name==obj.re_player?_this.setState({sysArr:[obj]}):'';

						}
						//account_name==obj.re_player?re_pos=obj.re_pos:'';
						if(account_name==obj.re_player){
							re_pos=obj.re_pos;
						}
						
					}
					_this.setState({getRedPaperInfoArr:arr});
					_this.loadGetRedPaperInfo(row+1);
				}else{
					let data=res.rows;
					let arr=_this.state.getRedPaperInfoArr;
					for(var i=0;i<data.length;i++){
						let obj=data[i];
						
						if(obj.re_id==_this.gameState.redPaperId){
							arr.push(obj);
							//account_name==obj.re_player?_this.setState({sysArr:[obj]}):'';
							if(account_name==obj.re_player){
								_this.setState({sysArr:[obj]})
							}
//							account_name==obj.re_player?alert(JSON.stringify(_this.state.sysObj),obj.re_player):'';
							console.log(obj.re_player+'领取了'+obj.re_amount+'===curr='+account_name);
							
						}
						//account_name==obj.re_player?re_pos=obj.re_pos:'';
						if(account_name==obj.re_player){
							re_pos=obj.re_pos
						}
					}
					_this.setState({getRedPaperInfoArr:arr,re_pos:re_pos});
				}

			}).catch(err=>{
				console.log(err)
//				alert('不能重复领取!')
			})
		})
		
	}
	getRedPaper(){//抢红包
		let user_name=localStorage.getItem("account_name");
		if(!user_name){
			alert('请登陆');
			return 
		}
		console.log(user_name+'。。。抢红包。。。')
		let eos=EOS(CONFIG);
		let _this=this;
		eos.contract(contract_name).then(contract=>{
			let random=Math.floor(Math.random()*100);
			contract.g4r4e(user_name,random,{authorization:[user_name]}).then(res=>{
				console.log('红包抢成功...');
//				_this.initPage();

				_this.checkGameRunTimeTable();
		
				_this.loadGetRedPaperInfo(0);
				
				let num=0;
				let stop=setInterval(()=>{//是否结束
					num++;
					console.log('getRedPaper 检测红包是否领取完...',num)
					if(_this.gameState.redPaperState==0){
						clearInterval(stop);
						_this.setState({isEnd:true});
						_this.setState({openRedBag:[<OpenRedBag data={_this.state.getRedPaperInfoArr} callBack={_this.cancelBag.bind(_this)}></OpenRedBag>],openBag:[],openBoaBag:[]});
						_this.setState({giveRedPaperBtn:[<div className="re-paper-btn" onClick={_this.giveRedPaper.bind(_this)}>发红包</div>]})
				
					}else if(num>10){
						clearInterval(stop);
						_this.setState({openRedBag:[<OpenRedBag data={_this.state.getRedPaperInfoArr} callBack={_this.cancelBag.bind(_this)}></OpenRedBag>],openBag:[],openBoaBag:[]});
						
					}
				},1600);
			}).catch(err=>{
				console.log(err);
				console.log('失败...');
				parseFloat(_this.gameState.account_balance)>4?alert('每个红包只可领取一次'):alert('余额不足'+parseFloat(_this.gameState.account_balance));
			});
		})
	}
	giveRedPaper(){//发红包

		let eos=EOS(CONFIG);
		let _this=this;
		let user_name=localStorage.getItem("account_name");
		if(!user_name){
			alert('请登陆');
			return 
		}
		eos.contract(contract_name).then(contract=>{
			contract.c4r4e(user_name,{authorization:[user_name]}).then(res=>{
//				_this.initPage();
				window.location.reload();
			}).catch(err=>{console.log(err)})
		});
		_this.setState({isEnd:false})
	}
	checkData(times){
//		times?"":times=5000;
		if(!times){
			times=5000
		}
		let _this=this;
		return setInterval(()=>{
			_this.initPage();
		},times);
	}
	redBagClick(type){
		if('openRedBag'==type){//红包点开后的弹窗
			let stop='';
			let _this=this;
			_this.getRedPaper();

		}else if('cancelRedBag'==type){
			this.setState({openRedBag:[]});
		}else if('cancelBoaBag'==type){
			this.setState({openBoaBag:[]});
		}else if('openBoaBag'==type&&this.state.boaRedBagNum>0){//幸运红包打开弹窗
			let boaNum=this.state.boaRedBagNum;
			this.setState({boaRedBagNum:boaNum-1<0?0:boaNum-1});
			this.setState({openBoaBag:[<OpenBoaBag callBack={this.cancelBag.bind(this)}></OpenBoaBag>]});
		}else if('openAlerts'==type){//规则
			this.setState({openAlerts:[<Alerts key='1' callBack={this.cancelRule.bind(this)}></Alerts>]})
		}else if('cancel'==type){
			this.setState({openAlerts:[]});
		}else if('openBag'==type){//红包打开弹窗
			this.setState({openBag:[<OpenBag callBack={this.cancelBag.bind(this)}></OpenBag>]})
		}else if('cancelOpenBag'==type){
			this.setState({openBag:[]})
		}else if("openLoading"==type){
			this.setState({addLoading:[<Loading></Loading>]});
		}else if("cancelLoading"==type){
			this.setState({addLoading:[]})
		}
	}
	componentDidMount(){

		let _this=this;
		_this.initPage();
		//_this.setState({stopCheckData:_this.checkData(6000)})//6s 更新一次数据
		//console.log(_this.state.stopCheckData);
//		setInterval(()=>{
//			_this.initPage();	
//		},3000)// 检测一次数据

	}
	componentWillMount(){

//		let _this=this;
//		
//		_this.redBagClick('openLoading');
//		setTimeout(function(){
//			_this.redBagClick('cancelLoading');
//		},1500);

	}
	
	cancelRule(){
		this.redBagClick('cancel');
	}
	cancelBag(type){
		this.redBagClick(type);	
	}
  render() {
    return (
      <div className="app wrap">
        <Nav></Nav>
        <RoomMess callBack={this.redBagClick.bind(this)} state={this.gameState} data={this.state} boaNum={this.state.boaRedBagNum}></RoomMess>
      	{this.state.openRedBag}
      	{this.state.openBoaBag}
      	{this.state.openAlerts}
      	{this.state.openBag}
      	{this.state.addLoading}
      	{this.state.giveRedPaperBtn}
      </div>
    );
  }
}
export default App;
